import React, { useEffect, useState } from "react";
import axios from "axios";
import SockJS from "sockjs-client";
import { Client } from "@stomp/stompjs";

const OsobaDetalji = ({ osobaId }) => {
  const [osoba, setOsoba] = useState(null);
  const [stompClient, setStompClient] = useState(null);

  useEffect(() => {
    // Učitaj podatke o osobi
    axios
      .get(`/api/osobe/${osobaId}`)
      .then((res) => setOsoba(res.data))
      .catch((err) => console.error(err));

    // WebSocket povezivanje
    const socket = new SockJS("/ws");
    const client = new Client({
      webSocketFactory: () => socket,
      reconnectDelay: 5000,
      onConnect: () => {
        client.subscribe(`/topic/like-updates/${osobaId}`, (msg) => {
          const noviBroj = JSON.parse(msg.body);
          setOsoba((prev) => ({ ...prev, brojLajkova: noviBroj }));
        });
      },
    });

    client.activate();
    setStompClient(client);

    return () => {
      client.deactivate();
    };
  }, [osobaId]);

  const handleToggleLike = () => {
    axios
      .post(`/api/like/toggle`, null, {
        params: { osobaId },
      })
      .catch(console.error);
  };

  if (!osoba) return <div>Učitavanje...</div>;

  return (
    <div style={{ padding: 20, border: "1px solid gray", borderRadius: 8 }}>
      <img
        src={`/uploads/${osoba.slikaUrl}`}
        alt="Osoba"
        style={{ width: 150 }}
      />
      <h2>
        {osoba.ime} {osoba.prezime}
      </h2>
      <p>
        <strong>Grad:</strong> {osoba.grad}
      </p>
      <p>
        <strong>Lepota:</strong> {osoba.lepota}/5
      </p>
      <p>
        <strong>Pamet:</strong> {osoba.pamet}/5
      </p>
      <p>
        <strong>Visina:</strong> {osoba.visina}/5
      </p>
      <p>
        <strong>Lajkova:</strong> {osoba.brojLajkova}
      </p>
      <button onClick={handleToggleLike}>Lajkuj / Unlajkuj</button>

      <h3>Komentari:</h3>
      <ul>
        {osoba.komentari.map((k, index) => (
          <li key={index}>{k.tekst}</li>
        ))}
      </ul>
    </div>
  );
};

export default OsobaDetalji;
