import React from "react";
import { Routes, Route } from "react-router-dom";
import Home from "./pages/Home";
import About from "./pages/About";
import Services from "./pages/Services";
import Contact from "./pages/Contact";
import Login from "./components/Login/Login";
import Register from "./components/register/Register";
import Admin from "./pages/Admin";
import User from "./pages/User";
import ProtectedRoute from "./services/ProtectedRoute";
import UserList from "./components/users/UserList";
import CreateUserForm from "./components/users/CreateUserForm";
// import OsobaDetalji from "./pages/OsobaDetalji"; // Import OsobaDetalji
// import OsobaDetalji from "./components/OsobaDetalji";
import OsobaDetaljiWrapper from "./components/Osoba/OsobaDetaljiWrapper";
import Osobe from "./components/Osoba/Osobe";

const AppRoutes = () => {
  return (
    <Routes>
      <Route path="/home" element={<Home />} />
      <Route path="/create" element={<CreateUserForm />} />
      <Route path="/osobe" element={<Osobe />} />
      <Route path="/services" element={<Services />} />
      <Route path="/contact" element={<Contact />} />
      <Route path="/login" element={<Login />} />
      <Route path="/register" element={<Register />} />
      <Route path="/admin" element={<Admin />} />
      <Route path="/users" element={<UserList />} />
      <Route
        path="/user"
        element={
          <ProtectedRoute roles={["user"]}>
            <User />
          </ProtectedRoute>
        }
      />
      <Route path="/osoba/:id" element={<OsobaDetaljiWrapper />} />
      {/* Add OsobaDetalji route */}
      <Route path="/unauthorized" element={<div>Unauthorized</div>} />
    </Routes>
  );
};

export default AppRoutes;
