import React, { useState } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import "./CreateUserForm.css"; // Importujte CSS datoteku za stilizaciju

const CreateUserForm = () => {
  const navigate = useNavigate();
  const [formData, setFormData] = useState({
    username: "",
    password: "",
    name: "",
    address: "",
    email: "",
    roles: [],
  });

  const handleRoleChange = (roleName) => {
    setFormData((prev) => {
      const roles = prev.roles.includes(roleName)
        ? prev.roles.filter((r) => r !== roleName)
        : [...prev.roles, roleName];
      return { ...prev, roles };
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    const payload = {
      ...formData,
      roles: formData.roles.map((name) => ({ name })),
    };
    try {
      await axios.post("http://localhost:8080/api/users/createUser", payload);
      alert("User created!");
      navigate("/users");
    } catch (error) {
      console.error(error);
      alert("Error creating user");
    }
  };

  return (
    <div className="container mt-5">
      <h3 className="mb-4">Create New User</h3>
      <form onSubmit={handleSubmit} className="card p-4 shadow-sm formaunosa">
        <div className="form-group">
          <label htmlFor="username" className="form-label">
            Username
          </label>
          <input
            id="username"
            className="form-control"
            onChange={(e) =>
              setFormData({ ...formData, username: e.target.value })
            }
          />
        </div>
        <div className="form-group">
          <label htmlFor="password" className="form-label">
            Password
          </label>
          <input
            id="password"
            type="password"
            className="form-control"
            onChange={(e) =>
              setFormData({ ...formData, password: e.target.value })
            }
          />
        </div>
        <div className="form-group">
          <label htmlFor="name" className="form-label">
            Name
          </label>
          <input
            id="name"
            className="form-control"
            onChange={(e) => setFormData({ ...formData, name: e.target.value })}
          />
        </div>
        <div className="form-group">
          <label htmlFor="address" className="form-label">
            Address
          </label>
          <input
            id="address"
            className="form-control"
            onChange={(e) =>
              setFormData({ ...formData, address: e.target.value })
            }
          />
        </div>
        <div className="form-group">
          <label htmlFor="email" className="form-label">
            Email
          </label>
          <input
            id="email"
            type="email"
            className="form-control"
            onChange={(e) =>
              setFormData({ ...formData, email: e.target.value })
            }
          />
        </div>

        <div className="mb-3">
          <label className="form-label">Roles</label>
          <div className="form-check">
            <input
              className="form-check-input"
              type="checkbox"
              id="roleUser"
              onChange={() => handleRoleChange("ROLE_USER")}
              checked={formData.roles.includes("ROLE_USER")}
            />
            <label className="form-check-label" htmlFor="roleUser">
              ROLE_USER
            </label>
          </div>
          <div className="form-check">
            <input
              className="form-check-input"
              type="checkbox"
              id="roleAdmin"
              onChange={() => handleRoleChange("ROLE_ADMIN")}
              checked={formData.roles.includes("ROLE_ADMIN")}
            />
            <label className="form-check-label" htmlFor="roleAdmin">
              ROLE_ADMIN
            </label>
          </div>
        </div>

        <button type="submit" className="btn btn-primary taster">
          Create User
        </button>
      </form>
    </div>
  );
};

export default CreateUserForm;
