import { jwtDecode } from "jwt-decode";

export const saveToken = (token) => {
  localStorage.setItem("token", token);
};

export const getToken = () => {
  return localStorage.getItem("token");
};

export const getUserRoles = () => {
  const token = getToken();
  if (token) {
    let decodedToken = {};
    try {
      decodedToken = jwtDecode(token);
      console.log(decodedToken.roles, "decodedToken");
      return (
        decodedToken.roles ||
        // decodedToken.authorities ||
        // decodedToken.realm_access?.roles ||
        []
      );
    } catch (error) {
      console.error("Failed to decode token:", error);
      return [];
    }

    // ✅ Ako koristiš `authorities` iz Spring Security
    //   if (decodedToken.authorities) {
    //     return decodedToken.authorities;
    //   }

    //   // ✅ Ako koristiš `roles` direktno
    //   if (decodedToken.roles) {
    //     return decodedToken.roles;
    //   }

    //   // ✅ Ako koristiš Keycloak (ili slično)
    //   if (decodedToken.realm_access?.roles) {
    //     return decodedToken.realm_access.roles;
    //   }

    //   return [];
  }
  return [];
};

export const isLoggedIn = () => {
  return !!getToken();
};

export const logout = () => {
  localStorage.removeItem("token");
};
