import React, { useEffect, useState } from "react";
import axios from "axios";
import "./Osobe.css"; // Import your CSS file for styling

const Osobe = () => {
  const [osobe, setOsobe] = useState([]);
  const [newOsoba, setNewOsoba] = useState({
    ime: "",
    prezime: "",
    grad: "",
    // slikaUrl: "",
    slika: null, // više nije slikaUrl, već fajl
    lepota: 1,
    pamet: 1,
    visina: 1,
    brojLajkova: 0,
  });
  const [showForm, setShowForm] = useState(false); // Kontrola prikaza forme

  useEffect(() => {
    axios
      .get("http://localhost:8080/api/osobe/all")
      .then((response) => {
        setOsobe(response.data);
        console.log(response.data, "response.dataeee==");
      })
      .catch((error) => console.error("Error fetching data: ", error));
  }, []);

  const handleEdit = (id) => {
    console.log("Edit osoba sa id:", id);
    // Ovdje možeš otvoriti formu za editovanje osobe ili preusmeriti korisnika na stranicu za editovanje
  };

  const handleDelete = (id) => {
    console.log("Delete osoba sa id:", id);
    axios
      .delete(`http://localhost:8080/api/osobe/${id}`)
      .then((response) => {
        // Po uspešnom brisanju ažuriraj stanje
        setOsobe(osobe.filter((osoba) => osoba.id !== id));
        console.log("Osoba obrisana:", response);
      })
      .catch((error) => console.error("Error deleting data:", error));
  };

  // Funkcija za dodavanje nove osobe
  // const handleAdd = () => {
  //   const formData = new FormData();
  //   formData.append("ime", newOsoba.ime);
  //   formData.append("prezime", newOsoba.prezime);
  //   formData.append("grad", newOsoba.grad);
  //   formData.append("lepota", newOsoba.lepota);
  //   formData.append("pamet", newOsoba.pamet);
  //   formData.append("visina", newOsoba.visina);
  //   formData.append("brojLajkova", newOsoba.brojLajkova);
  //   formData.append("slika", newOsoba.slika); // ovo je File objekat

  //   axios
  //     .post("http://localhost:8080/api/osobe/createOsobaPrva", formData, {
  //       headers: {
  //         "Content-Type": "multipart/form-data",
  //       },
  //     })
  //     .then((response) => {
  //       setOsobe([...osobe, response.data]);
  //       setNewOsoba({
  //         ime: "",
  //         prezime: "",
  //         grad: "",
  //         slika: null,
  //         lepota: 1,
  //         pamet: 1,
  //         visina: 1,
  //         brojLajkova: 0,
  //       });
  //       setShowForm(false);
  //       console.log("Nova osoba dodata:", response);
  //     })
  //     .catch((error) => console.error("Greška pri dodavanju osobe:", error));
  // };

  const handleAdd = () => {
    const formData = new FormData();
    formData.append("ime", newOsoba.ime);
    formData.append("prezime", newOsoba.prezime);
    formData.append("grad", newOsoba.grad);
    formData.append("lepota", newOsoba.lepota);
    formData.append("pamet", newOsoba.pamet);
    formData.append("visina", newOsoba.visina);
    formData.append("brojLajkova", newOsoba.brojLajkova);
    formData.append("slika", newOsoba.slika); // Ovde dodajemo fajl

    axios
      .post("http://localhost:8080/api/osobe/createOsoba", formData, {
        headers: {
          "Content-Type": "multipart/form-data", // Dodajemo header za form-data
        },
      })
      .then((response) => {
        setOsobe([...osobe, response.data]);
        setNewOsoba({
          ime: "",
          prezime: "",
          grad: "",
          slika: null,
          lepota: 1,
          pamet: 1,
          visina: 1,
          brojLajkova: 0,
        });
        setShowForm(false);
        console.log("Nova osoba dodata:", response);
      })
      .catch((error) => console.error("Error adding osoba:", error));
  };

  // Funkcija za promenu vrednosti u formi
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setNewOsoba((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  return (
    <div className="container">
      <div className="row">
        <button
          className="btn btn-success mb-3"
          onClick={() => setShowForm(!showForm)} // Toggles form visibility
        >
          Dodaj Osobu
        </button>

        {showForm && (
          <div className="mb-3">
            <h3>Dodaj Novu Osobu</h3>
            <form>
              <input
                type="text"
                name="ime"
                placeholder="Ime"
                value={newOsoba.ime}
                onChange={handleInputChange}
                className="form-control mb-2"
              />
              <input
                type="text"
                name="prezime"
                placeholder="Prezime"
                value={newOsoba.prezime}
                onChange={handleInputChange}
                className="form-control mb-2"
              />
              <input
                type="text"
                name="grad"
                placeholder="Grad"
                value={newOsoba.grad}
                onChange={handleInputChange}
                className="form-control mb-2"
              />
              {/* <input
                type="text"
                name="slikaUrl"
                placeholder="Slika URL"
                value={newOsoba.slikaUrl}
                onChange={handleInputChange}
                className="form-control mb-2"
              /> */}
              <input
                type="file"
                name="slika"
                onChange={(e) => {
                  setNewOsoba((prev) => ({
                    ...prev,
                    slika: e.target.files[0],
                  }));

                  console.log(e.target.files[0], "e.target");
                }}
                className="form-control mb-2"
              />

              <input
                type="number"
                name="lepota"
                placeholder="Lepota (1-5)"
                value={newOsoba.lepota}
                onChange={handleInputChange}
                className="form-control mb-2"
              />
              <input
                type="number"
                name="pamet"
                placeholder="Pamet (1-5)"
                value={newOsoba.pamet}
                onChange={handleInputChange}
                className="form-control mb-2"
              />
              <input
                type="number"
                name="visina"
                placeholder="Visina (1-5)"
                value={newOsoba.visina}
                onChange={handleInputChange}
                className="form-control mb-2"
              />
              <input
                type="number"
                name="brojLajkova"
                placeholder="Broj Lajkova"
                value={newOsoba.brojLajkova}
                onChange={handleInputChange}
                className="form-control mb-2"
              />
              <button
                type="button"
                className="btn btn-primary"
                onClick={handleAdd}
              >
                Dodaj Osobu
              </button>
            </form>
          </div>
        )}

        <div className="table-responsive">
          <table className="table table-secondary table-striped">
            <thead>
              <tr className="headingTable">
                <th scope="col">Slika</th>
                <th scope="col">Ime</th>
                <th scope="col">Prezime</th>
                <th scope="col">Grad</th>
                <th scope="col">Lepota </th>
                <th scope="col">Pamet </th>
                <th scope="col">Visina </th>
                <th scope="col">Broj Lajkova</th>
                <th scope="col"> Edit</th>
                <th scope="col"> Delete</th>
              </tr>
            </thead>
            <tbody>
              {osobe?.map((data) => (
                <tr key={data.id}>
                  <td>
                    <img
                      src={`data:image/jpeg;base64,${data.slika}`}
                      alt={`${data.ime} ${data.prezime}`}
                      style={{
                        width: "40px",
                        height: "40px",
                        borderRadius: "50%",
                      }}
                    />
                  </td>
                  <td>{data.ime}</td>
                  <td>{data.prezime}</td>
                  <td>{data.grad}</td>
                  <td>{data.lepota}</td>
                  <td>{data.pamet}</td>
                  <td>{data.visina}</td>
                  <td>{data.brojLajkova}</td>
                  <td>
                    <button
                      className="btn btn-primary btn-sm"
                      onClick={() => handleEdit(data.id)}
                    >
                      Edit
                    </button>
                  </td>
                  <td>
                    <button
                      className="btn btn-danger btn-sm ml-2"
                      onClick={() => handleDelete(data.id)}
                    >
                      Delete
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
};

export default Osobe;
